import React, { useEffect, useState } from "react";
import { getCGTokens } from "../../helpers";
import orderBy from "lodash/orderBy";

var formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "PHP",
});

export const TokenTable = () => {
  const [state, setState] = useState({ tokens: [], loading: true });
  const { tokens, loading } = state;
  useEffect(() => {
    initTable();
  }, []);
  const initTable = async () => {
    try {
      const result = await getCGTokens();
      setState((prev) => ({ ...prev, loading: false, tokens: result }));
    } catch (error) {
      setState((prev) => ({ ...prev, loading: false, tokens: [] }));
    }
  };
  return (
    <table className="table table-dark table-boredered table-striped table-borderless">
      <tr>
        <th className="text-secondary text-start p-3 fw-light">
          Cryptocurrency
        </th>
        <th className="text-secondary text-start p-3 fw-light">Today</th>
        <th className="text-secondary p-3 fw-light">24Hrs</th>
        {/* <th className="text-secondary p-3 fw-light">Market Cap</th> */}
      </tr>
      {loading && "Loading..."}
      {orderBy(tokens, ["php_24h_change"], ["desc"]).map((token, index) => {
        return (
          <tr key={index}>
            <td className="d-flex align-items-center text-start p-1 ">
              <img width={30} src={token.icon} />
              {token.name} ({token.abbr})
            </td>
            <td className="text-start">
              {formatter.format(token.php)}{" "}
              <span className="text-secondary">(${token.usd})</span>
            </td>
            <td
              className={
                token.php_24h_change > 0 ? "text-success" : "text-danger"
              }
            >
              {token.php_24h_change.toFixed(2)} %
            </td>
            {/* <td>{token.php_market_cap}</td> */}
          </tr>
        );
      })}
    </table>
  );
};

